.projectsBlock {
    background: #33183a;
}

.projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    color: #F1F0CC;
}

.projects {
    width: 100%;
    display: flex;
    justify-content: space-between;
    text-align: center;
    flex-wrap: wrap;
}

@media screen {
    @media (max-width: 983px) {
        .projects {
            justify-content: center;
        }
    }
}

